import { processDocument } from "./redactionEngine";
import type { ProcessDocumentInput, ProcessedDocument } from "./types/processing";

/**
 * Warstwa kliencka – w wersji desktopowej może delegować przetwarzanie
 * do procesu backendowego (np. Rust/Tauri, Node/Electron) z OCR i LLM.
 */
export async function processDocumentClient(
  input: ProcessDocumentInput,
): Promise<ProcessedDocument> {
  // W trybie webowym wszystko dzieje się lokalnie w przeglądarce.
  return processDocument(input);
}


